import React, { memo, useState, useEffect } from "react";

import { CSVReader  } from "react-papaparse";
import axios from "axios";

import {
    Button,
    Card,
    CardHeader,
    CardBody,
    CardFooter,
    CardTitle,
    Label,
    FormGroup,
    Form,
    Input,
    FormText,
    Table,
    Row,
    Col
  } from "reactstrap";

const Upload = () => {
    const [csvKeysData, setCsvKeysData] = useState([]);
    const [csvValuesData, setCsvValuesData] = useState([]);

    const [viewKeysData, setViewKeysData] = useState([]);
    const [viewValuesData, setViewValuesData] = useState([]);


    const handleOnDrop = (data) => {
        const csvKeys = data.slice(0, 1);
        const csvValues = data.slice(1, data.length);

        console.log(csvKeys);
        console.log(csvValues);

        let viewKeys = [];
        let viewValues = [];

        const columns = csvKeys[0].data;
        const columnsLength = columns.length;
        const columnsLimited = 5;
        if(columnsLength <= columnsLimited * 2) {
            viewKeys = columns;
        } else {
            for (let i = 0; i < columnsLimited; i++) {
                const e = columns[i];
                viewKeys.push(e);
            }
            viewKeys.push(".  .  .");
            for (let i = columnsLength-columnsLimited-1; i < columnsLength; i++) {
                const e = columns[i];
                viewKeys.push(e);
            }
        }

        const rowsLength = csvValues.length;
        const rowsLimited = 5;
        if(rowsLength <= rowsLimited * 2) {
            viewValues = csvValues;
        } else {
            for (let i = 0; i < rowsLimited; i++) {
                const viewValue = [];

                const e = csvValues[i].data;
                const eLength = e.length;

                for (let idx = 0; idx < rowsLimited; idx++) {
                    const ele = e[idx];
                    viewValue.push(ele);
                }
                viewValue.push(".  .  .");
                for (let idx = eLength-rowsLimited-1; idx < eLength; idx++) {
                    const ele = e[idx];
                    viewValue.push(ele);
                }

                viewValues.push({"data": viewValue});
            }

            for (let i = rowsLimited; i < rowsLimited + 3; i++) {
                const viewValue = [];
            
                const e = csvValues[i].data;
                const eLength = e.length;

                for (let idx = 0; idx < rowsLimited; idx++) {
                    viewValue.push("");
                }
                viewValue.push(".  .  .");
                for (let idx = eLength-rowsLimited - 1; idx < eLength; idx++) {
                    viewValue.push("");
                }


                viewValues.push({"data": viewValue});
            }

            for (let i = rowsLength-rowsLimited - 1; i < rowsLength; i++) {
                const viewValue = [];

                const e = csvValues[i].data;
                const eLength = e.length;

                for (let idx = 0; idx < rowsLimited; idx++) {
                    const ele = e[idx];
                    viewValue.push(ele);
                }
                viewValue.push(".  .  .");
                for (let idx = eLength-rowsLimited - 1; idx < eLength; idx++) {
                    const ele = e[idx];
                    viewValue.push(ele);
                }

                viewValues.push({"data": viewValue});
            }
            
        }

        setCsvKeysData(csvKeys);
        setCsvValuesData(csvValues);
        setViewKeysData(viewKeys);
        setViewValuesData(viewValues);
    }
    
    const handleOnError = (err, file, inputElem, reason) => {
        console.log(err)
    }
    
    const handleOnRemoveFile = (data) => {
        setCsvKeysData([]);
        setCsvValuesData([]);
        setViewKeysData([]);
        setViewValuesData([]);
    }

    useEffect(() => {
        
    }, [])

    return (
        <>
            <div className="content">
                <Row>
                    <Col md="12">
                        <Card>
                            <CardHeader>
                            <CardTitle tag="h4">DATA UPLOAD</CardTitle>
                            </CardHeader>
                            <CardBody>
                                <Row>
                                    <Col md="6">
                                        <Form action="#" method="#">
                                            <FormGroup>
                                                <CSVReader
                                                    onDrop={handleOnDrop}
                                                    onError={handleOnError}
                                                    addRemoveButton
                                                    removeButtonColor='#659cef'
                                                    onRemoveFile={handleOnRemoveFile}
                                                >
                                                    <span>Drop CSV file here or click to upload.</span>
                                                </CSVReader>
                                            </FormGroup>
                                        </Form>
                                    </Col>
                                </Row>
                            </CardBody>

                            <CardHeader>
                            <CardTitle tag="h4">DATA COLUMNS</CardTitle>
                            </CardHeader>
                            <CardBody>
                                <span>
                                    {
                                        csvKeysData.length !== 0 
                                        ?
                                        csvKeysData.map((v, i) => {
                                            const { data } = v;

                                            return (
                                                <>
                                                <strong key={data}>
                                                    <em>
                                                        {data + " "}
                                                    </em>
                                                </strong>
                                                </>
                                            )
                                        })
                                        :
                                        ""
                                    }
                                </span>
                            </CardBody>

                            <CardHeader>
                            <CardTitle tag="h4">DATA STATUS</CardTitle>
                            </CardHeader>
                            <CardBody>
                                <Table responsive>
                                    <thead>
                                        <tr>
                                            {
                                                viewKeysData.length !== 0
                                                ?
                                                viewKeysData.map((v) => {
                                                    const { data } = v;

                                                    return (
                                                        <>
                                                        {
                                                            <th key={v} className="text-left">
                                                                {v}
                                                            </th>
                                                        }
                                                        </>
                                                    )
                                                })
                                                :
                                                <>
                                                    <th className="text-left">Column-1</th>
                                                    <th className="text-left">Column-2</th>
                                                    <th className="text-left">Column-3</th>
                                                    <th className="text-left">Column-4</th>
                                                    <th className="text-left">Column-5</th>
                                                </>
                                            }
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            viewValuesData.length !== 0
                                            ?
                                            viewValuesData.map((v) => {
                                                const { data } = v;

                                                return (
                                                    <>
                                                        <tr key={v}>
                                                            {
                                                                data.map((val) => {
                                                                    return (
                                                                        <>
                                                                            <td key={val} className="text-left">
                                                                                {val}
                                                                            </td>
                                                                        </>
                                                                    )
                                                                })
                                                            }
                                                        </tr>
                                                    </>
                                                )
                                            })
                                            :
                                            <>
                                                <tr>
                                                    <td className="text-left">Sample-1.1</td>
                                                    <td className="text-left">Sample-1.2</td>
                                                    <td className="text-left">Sample-1.3</td>
                                                    <td className="text-left">Sample-1.4</td>
                                                    <td className="text-left">Sample-1.5</td>
                                                </tr>
                                                <tr>
                                                    <td className="text-left">Sample-2.1</td>
                                                    <td className="text-left">Sample-2.2</td>
                                                    <td className="text-left">Sample-2.3</td>
                                                    <td className="text-left">Sample-2.4</td>
                                                    <td className="text-left">Sample-2.5</td>
                                                </tr>
                                            </>
                                        }
                                    </tbody>
                                </Table>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
            </div>
        </>
    );
}

export default memo(Upload);
